  }
  .legend-content > li {
    margin-bottom: 3px;
    display: flex;
    align-items: center;
    /* text-transform: uppercase; */
  }
  .legend-content > li:last-child {
    margin-bottom: 0;
  }
  .circle {
    width: 10px;
